  line-height: 1.29;
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

button {
  background-color: var(--color-btn);
  fill: var(--color-btn-fill);
  color: var(--color-btn-fill);
  border: none;
  border-radius: 30px;
  transition: background-color .25s ease-in-out;
}

button:hover, button:focus {
  background-color: var(--color-btn-active);
}

button:disabled {
  background-color: var(--color-btn);
  fill: var(--color-btn-fill);
}

#btn-up-section {
  position: fixed;
  bottom: 100px;
  right: 20px;
}

.btn-up {
  background-color: var(--color-btn-up);
  fill: var(--color-btn-up-fill);
  cursor: pointer;
  transition: transform var(--animation-duration) var(--timing-function), fill var(--animation-duration) var(--timing-function), box-shadow var(--animation-duration) var(--timing-function);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  display: flex;
  transform: scale(0);
}

.btn-up:hover, .btn-up:focus {
  fill: var(--color-btn-up-fill-active);
  background-color: var(--color-btn-up);
  box-shadow: 1px 2px 1px #2e2f4214, 1px 1px 1px #2e2f4229, 1px 1px 6px #2e2f4214;
}

