.loader {
  background-repeat: no-repeat;
  background-image: linear-gradient(var(--color-car-trunk-light), var(--color-car-trunk-dark)), linear-gradient(var(--color-car-cabin-light), var(--color-car-cabin-dark)), linear-gradient(var(--color-car-cabin-light), var(--color-car-cabin-dark));
  background-position: 0 0, 80px 20px, 100px 40px;
  background-size: 80px 70px, 30px 50px, 30px 30px;
  width: 130px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
}

.loader:after {
  content: "";
  box-sizing: content-box;
  border: 10px solid var(--color-car-wheel);
  box-shadow: 78px 0 0 -10px #fff, 78px 0 var(--color-car-wheel);
  background: #fff;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  animation: .75s ease-in infinite alternate wheelSk;
  position: absolute;
  bottom: 10px;
  left: 12px;
}

.loader:before {
  content: "";
  background-image: linear-gradient(#fff 45px, #0000 0), linear-gradient(#fff 45px, #0000 0), linear-gradient(#fff 45px, #0000 0);
  background-position: 0 11px, 8px 35px, 0 60px;
  background-repeat: no-repeat;
  background-size: 30px 4px;
  width: 70px;
  height: 70px;
  animation: .75s linear infinite lineDropping;
  position: absolute;
  top: 0;
  right: 100%;
}

@keyframes wheelSk {
  0%, 50%, 100% {
    transform: translateY(0);
  }

  30%, 90% {
    transform: translateY(-3px);
  }
}

@keyframes lineDropping {
  0% {
    opacity: 1;
    background-position: 100px 11px, 115px 35px, 105px 60px;
  }

  50% {
    background-position: 0 11px, 20px 35px, 5px 60px;
  }

  60% {
    background-position: -30px 11px, 0 35px, -10px 60px;
  }

  75%, 100% {
    opacity: 0;
    background-position: -30px 11px, -30px 35px, -30px 60px;
  }
}

.backdrop-modal .loader {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}



